import { Icon } from "@iconify/react";
import menu2Fill from "@iconify/icons-eva/menu-2-fill";
// material
import { alpha, styled } from "@material-ui/core/styles";
import { Box, Stack, AppBar, Toolbar, IconButton } from "@material-ui/core";
// components
import { MHidden } from "../../../../../components/@material-extend";
//
import Searchbar from "./Searchbar";
import AccountPopover from "./AccountPopover";
import NotificationsPopover from "./NotificationsPopover";
import DoneeLogo from "../../../../../assets/icons/doneelogo.svg";
import { useNavigate } from "react-router";

// ----------------------------------------------------------------------

const APPBAR_MOBILE = 64;
const APPBAR_DESKTOP = 60;

const RootStyle = styled(AppBar)(({ theme }) => ({
  boxShadow: "none",
  backdropFilter: "blur(6px)",
  WebkitBackdropFilter: "blur(6px)", // Fix on Mobile
  background: "linear-gradient(90deg, #0077b6 23.6%, #32b2ed 100%)",
  width: `100%`,
  height: APPBAR_DESKTOP,
  justifyContent: "center",
}));

const ToolbarStyle = styled(Toolbar)(({ theme }) => ({
  minHeight: APPBAR_MOBILE,
  [theme.breakpoints.up("lg")]: {
    minHeight: APPBAR_DESKTOP,
    padding: theme.spacing(0, 3),
  },
}));

// ----------------------------------------------------------------------

interface DashboardNavbarProps {
  onOpenSidebar: () => void;
}
export default function DashboardNavbar({
  onOpenSidebar,
}: DashboardNavbarProps) {
  const navigate = useNavigate();
  return (
    <RootStyle>
      <ToolbarStyle>
        <img
          style={{ cursor: "pointer" }}
          src={DoneeLogo}
          alt="logo"
          className="navIcons"
          onClick={() => navigate("../")}
        />

        <Box sx={{ flexGrow: 1 }} />

        <Stack
          direction="row"
          alignItems="center"
          spacing={{ xs: 0.5, sm: 1.5 }}
        >
          <AccountPopover />
          <NotificationsPopover iconColor="white" />
          <MHidden width="lgUp">
            <IconButton onClick={onOpenSidebar} sx={{ mr: 1, color: "white" }}>
              <Icon icon={menu2Fill} />
            </IconButton>
          </MHidden>
        </Stack>
      </ToolbarStyle>
    </RootStyle>
  );
}
