import { Icon } from "@iconify/react";
import { useRef, useState } from "react";
import homeFill from "@iconify/icons-eva/home-fill";
import personFill from "@iconify/icons-eva/person-fill";
import { Link as RouterLink } from "react-router-dom";
// material
import { alpha } from "@material-ui/core/styles";
import {
  Button,
  Box,
  Divider,
  MenuItem,
  Typography,
  Avatar,
  IconButton,
} from "@material-ui/core";

import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

// components
import {
  ILoginResponse,
  RootAppState,
  UserDetailProps,
} from "../../../../../utils/typeHelpers";
import { useQuery, UseQueryResult } from "react-query";
// import { GET_DONOR_PROFILE_API } from "../../../../../utils/Api";
import { tokenInitialValue } from "../../../../../utils/initialvalues";
import { saveLoginAction } from "../../../../../redux/actions/loginAction";
import { saveUserInfoAction } from "../../../../../redux/actions/saveUserInfoAction";
import MenuPopover from "../../../../../components/MenuPopover";

import ErrorCatchComponent from "../../../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";
import Loader from "../../../../../components/Loader";
import useAxios from "../../../../../utils/useAxios";

// ----------------------------------------------------------------------

const MENU_OPTIONS = [
  {
    label: "Home",
    icon: homeFill,
    linkTo: "/",
  },
  {
    label: "Profile",
    icon: personFill,
    linkTo: "/profile",
  },
  // {
  //   label: "Settings",
  //   icon: settings2Fill,
  //   linkTo: "#",
  // },
];

// ----------------------------------------------------------------------

export default function AccountPopover() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const anchorRef = useRef(null);
  const [open, setOpen] = useState(false);
  const access = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.access
  );

  // const { data }: UseQueryResult<UserDetailProps, Error> = useQuery(
  //   ["userInfo", access],
  //   () => GET_DONOR_PROFILE_API(access)
  // );

  const axiosInstance = useAxios();
  const { data, isLoading, isError, error, isSuccess }: UseQueryResult<UserDetailProps, Error> = useQuery(
    ["userInfo", access],
    async () => {
      const response = await axiosInstance.get(`/user-detail/`);

      return response.data;
    }
  );

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleLogout = () => {
    const userInfo: ILoginResponse = tokenInitialValue;

    dispatch(saveLoginAction(false));
    dispatch(saveUserInfoAction(userInfo));
    navigate("../");
  };

  // console.log('==-- co###--==');
  
  return (
    <>
      <ErrorCatchComponent>
      {isLoading === false ? (
        <div>
          {isError ? (
            <ApiErrorPage
              error={error ? error : new Error("Network response was not ok.")}
            />
          ) : (
           <div>
              <IconButton
                ref={anchorRef}
                onClick={handleOpen}
                sx={{
                  padding: 0,
                  width: 44,
                  height: 44,
                  ...(open && {
                    "&:before": {
                      zIndex: 1,
                      content: "''",
                      width: "100%",
                      height: "100%",
                      borderRadius: "50%",
                      position: "absolute",
                      bgcolor: (theme) => alpha(theme.palette.grey[900], 0.72),
                    },
                  }),
                }}
              >
                <Avatar src={data?.image} alt="photoURL" />
              </IconButton>

              <MenuPopover
                open={open}
                onClose={handleClose}
                anchorEl={anchorRef.current}
                sx={{ width: 220 }}
              >
                <Box sx={{ my: 1.5, px: 2.5 }}>
                  <Typography variant="subtitle1" noWrap>
                    {data?.full_name}
                  </Typography>
                  <Typography variant="body2" sx={{ color: "text.secondary" }} noWrap>
                    @{data?.username}
                  </Typography>
                </Box>

                <Divider sx={{ my: 1 }} />

                {MENU_OPTIONS.map((option) => (
                  <MenuItem
                    key={option.label}
                    to={option.linkTo}
                    component={RouterLink}
                    onClick={handleClose}
                    sx={{
                      color: "#6C757D",
                      fontSize: 20,
                      typography: "body2",
                      py: 1,
                      px: 2.5,
                    }}
                  >
                    <Box
                      component={Icon}
                      icon={option.icon}
                      sx={{
                        color: "#CED4DA",
                        mr: 2,
                        width: 24,
                        height: 24,
                      }}
                    />

                    {option.label}
                  </MenuItem>
                ))}

                <Box sx={{ p: 2, pt: 1.5 }}>
                  <Button
                    onClick={handleLogout}
                    fullWidth
                    color="inherit"
                    variant="outlined"
                  >
                    Logout
                  </Button>
                </Box>
              </MenuPopover>
          </div>  
          )}
        </div>
      ) : (
        <Loader />
      )}
      </ErrorCatchComponent>

    </>
  );
}
