import { useState } from "react";
import { Outlet } from "react-router-dom";
// material
import { styled } from "@material-ui/core/styles";
import DashboardNavbar from "../screen/Home/NGO/Dashboard/components/DashboardNavbar";
import DashboardSidebar from "../screen/Home/NGO/Dashboard/components/DashboardSidebar";
import { useSelector } from "react-redux";
import { ProfileDetailType, RootAppState } from "../utils/typeHelpers";
import { useQuery, UseQueryResult } from "react-query";
// import { GET_PROFILE_DETAIL } from "../utils/Api";
import Loader from "../components/Loader";

import ErrorCatchComponent from "../components/ErrorCatchComponent";
import ApiErrorPage from "../screen/Errror/ApiErrorPage";
import useAxios from "../utils/useAxios";
//

// ----------------------------------------------------------------------

const APP_BAR_MOBILE = 64;
const APP_BAR_DESKTOP = 92;

const RootStyle = styled("div")({
  display: "flex",
  minHeight: "100%",
  overflow: "hidden",
  backgroundColor: "#F8F9FA",
});

const MainStyle = styled("div")(({ theme }) => ({
  flexGrow: 1,
  overflow: "auto",
  minHeight: "100%",
  paddingTop: APP_BAR_MOBILE + 24,
  paddingBottom: theme.spacing(10),
  [theme.breakpoints.up("lg")]: {
    paddingTop: APP_BAR_DESKTOP + 24,
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
  },
}));

// ----------------------------------------------------------------------

export default function DashboardLayout() {
  const [open, setOpen] = useState(false);
  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  // const { data, isLoading }: UseQueryResult<ProfileDetailType, Error> =
  //   useQuery(["profileDetailType", userState.userInfo.access], () =>
  //     GET_PROFILE_DETAIL(userState.userInfo.access)
  //   );

  const axiosInstance = useAxios();
  const { data, isLoading, isError, error }: UseQueryResult<ProfileDetailType, Error> = useQuery(
    ["profileDetailType", userState.userInfo.access],
    async () => {
      const response = await axiosInstance.get(`/profile-detail/`);

      return response.data;
    }
  );

  const onCloseSidebar: any = () => setOpen(false);

  // console.log('--- c-o-mm-ing---')
  return (
    <ErrorCatchComponent>
    {isLoading === false ? (
      <div>
        {isError ? (
          <ApiErrorPage
            error={error ? error : new Error("Network response was not ok.")}
          />
        ) : (
         
          <RootStyle>
            <DashboardNavbar onOpenSidebar={() => setOpen(true)} />
            <DashboardSidebar
              username={data ? data.full_name : ""}
              profileImage={data ? data.image : ""}
              isOpenSidebar={open}
              onCloseSidebar={onCloseSidebar}
            />
            <MainStyle>
              <Outlet />
            </MainStyle>
          </RootStyle>
          
        )}
      </div>
    ) : (
      <Loader />
    )}
  </ErrorCatchComponent>
  
  );
}
